@import "variables";



.slider {
  .slide {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0));
    }
  }

  .image {
    height: 100%;
    width: 100%;
    background: {
      repeat: no-repeat;
      size: cover;
    };
  }

  img {
    width: 100%;
  }

  .slide-dscr {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 70px 55px;
    display: flex;
    justify-content: space-between;
    z-index: 1;
  }

  h2 {
    line-height: 1.3;
    color: $white;
    margin-bottom: 50px;
  }

  h4 {
    line-height: 1.2;
  }

  .col {
    position: relative;
    max-width: 70%;
  }

  //main slider on home page
  &.main-slider {
    height: 70vh;  //660px;

    .slide {
      height: 70vh;  //660px;
    }
  }

  //slider with nav
  &.slider-nav {
    width: 145px;
    height: 100vh;
    .slide {
      height: 145px;
    }
  }

  //slider for
  &.slider-for {
    width: calc(100% - 545px - 165px);
    height: 100vh;
    .slide {
      height: 100vh;
    }
  }
}

.slick-slider {
  line-height: 1;
}

.slick-dots {
  @extend .horiz-align;
  top: 70px;

  li {
    display: inline-block;
    font-size: 0;
    margin: 0 5px;
    &.slick-active {
      button {
        background: $white;
      }
    }
  }
  button {
    height: 4px;
    width: 30px;
    padding: 0;
    font-size: 0;
    color: transparent;
    border: none;
    background: $white-transp;
    cursor: pointer;
  }
}

.slick-arrow {
  color: transparent;
  border: none;
  background: transparent;
  font-size: 0;
  padding: 0;
  @extend .vert-align;
  cursor: pointer;
  &:before {
    font-family: 'clife-icons';
    font-size: 2.813rem;  //45px
    color: $white;
  }


  &.slick-prev {
    left: 30px;
    &:before {
      @extend .clife-ico-arrow-thin-l;
    }
  }

  &.slick-next {
    right: 30px;
    &:before {
      @extend .clife-ico-arrow-thin-r;
    }
  }
}




@media screen and ($tablet-min) and ($desktop-medium) {
  .slider {
    .slide-dscr {
      padding: 30px 50px;
    }

    h2 {
      margin-bottom: 30px;
    }

    &.main-slider {
      height: 65vh;

      .slide {
        height: 65vh;
      }
    }
  }
}



@media screen and ($tablet) {
  .slider {
    .col {
      max-width: 100%;
    }

    .slide-dscr {
      flex-direction: column;
      justify-content: space-between;
      padding: 100px 30px 30px;
    }
  }
}



@media screen and ($mobile) {
  .slider {
    .slide-dscr {
      padding: 90px 15px 0;
    }

    h2 {
      font-size: 28px;
      margin-bottom: 25px;
    }

    &.main-slider {
      height: 100vh;

      .slide {
        height: 100vh;
      }
    }
  }

  .slick-dots {
    top: 50px;
  }
}